export default (editor, opts = {}) => {
    const { label, idToolbarHead, idPageButton, idToolbarFoot, idToolbarMatchQueryCount } = opts;

    editor.DomComponents.addType(idToolbarMatchQueryCount, {
        model: {
            defaults: {
                name: 'Show match query',
                tagName: 'div',
                dragable: false,
                dropable: false,
                editable: false,
                attributes: {
                    class: 'w-100',
                },
                components: [
                    {
                        tagName: "span",
                        dragable: false,
                        dropable: false,
                        editable: false,
                        attributes: {
                            "data-x-cls": "my-product-toolbar-query-count",
                        },
                        components: `0`,
                    },
                    {
                        tagName: "span",
                        dragable: false,
                        dropable: false,
                        editable: false,
                        attributes: {
                            class: "ml-1",
                        },
                        components: `${editor.I18n.t("grapesjs-plugin-my-products.resultCount")}`,
                    },
                ]
            }
        }
    })

    editor.DomComponents.addType(idToolbarHead, {
        model: {
            defaults: {
                name: `${label} Toolbar Head`,
                dropable: false,
                dragable: false,
                tagName: 'div',
                attributes: {
                    class: 'row mb-3',
                    "data-x-cls": "my-product-header",
                },
                components: [
                    {
                        tagName: 'div',
                        dropable: false,
                        dragable: false,
                        editable: false,
                        attributes: {
                            class: 'col-sm-12 col-md-6 col-lg-6 col-xl-8 p-1',
                        },
                        components: [
                            {
                                tagName: 'input',
                                dropable: false,
                                dragable: false,
                                editable: false,
                                attributes: {
                                    type: "text",
                                    class: "form-control w-100",
                                    placeholder: `${editor.I18n.t("grapesjs-plugin-my-products.search")}...`,
                                    "data-x-cls": "my-product-header--search",
                                },
                            }
                        ],
                    },
                    {
                        tagName: 'div',
                        dropable: false,
                        dragable: false,
                        editable: false,
                        attributes: {
                            class: 'col-sm-8 col-md-4 col-lg-3 col-xl-2 p-1',
                        },
                        components: [
                            {
                                tagName: 'select',
                                dropable: false,
                                dragable: false,
                                editable: false,
                                attributes: {
                                    class: "form-control w-100",
                                    "data-x-cls": "my-product-header--sort",
                                },
                                components: [
                                    {
                                        tagName: 'option',
                                        dropable: false,
                                        dragable: false,
                                        editable: false,
                                        attributes: {
                                            value: "suitable",
                                            selected: "selected",
                                        },
                                        components: `${editor.I18n.t("grapesjs-plugin-my-products.relateSuitable")}`,
                                    },
                                    {
                                        tagName: 'option',
                                        dropable: false,
                                        dragable: false,
                                        editable: false,
                                        attributes: {
                                            value: "price-a-z",
                                        },
                                        components: `${editor.I18n.t("grapesjs-plugin-my-products.pricesLowsToHights")}`,
                                    },
                                    {
                                        tagName: 'option',
                                        dropable: false,
                                        dragable: false,
                                        editable: false,
                                        attributes: {
                                            value: "price-z-a",
                                        },
                                        components: `${editor.I18n.t("grapesjs-plugin-my-products.pricesHightsToLows")}`,
                                    },
                                ]
                            }
                        ],
                    },
                    {
                        tagName: 'div',
                        dropable: false,
                        dragable: false,
                        editable: false,
                        attributes: {
                            class: 'col-sm-4 col-md-2 col-lg-3 col-xl-2 p-1',
                        },
                        components: [
                            {
                                tagName: 'select',
                                dropable: false,
                                dragable: false,
                                editable: false,
                                attributes: {
                                    class: "form-control w-100",
                                    name: "my-product-toolbar-display",
                                },
                                components: [
                                    {
                                        tagName: 'option',
                                        dropable: false,
                                        dragable: false,
                                        editable: false,
                                        attributes: {
                                            value: "block",
                                            selected: "selected",
                                        },
                                        components: `${editor.I18n.t("grapesjs-plugin-my-products.displayBlock")}`,
                                    },
                                    {
                                        tagName: 'option',
                                        dropable: false,
                                        dragable: false,
                                        editable: false,
                                        attributes: {
                                            value: "list",
                                        },
                                        components: `${editor.I18n.t("grapesjs-plugin-my-products.displayList")}`,
                                    },
                                ]
                            }
                        ]
                    },
                    {
                        type: idToolbarMatchQueryCount,
                        dropable: false,
                        dragable: false,
                        editable: false,
                        attributes: {
                            class: 'col-sm-12 col-md-6 col-lg-6 col-xl-8 p-1',
                        },
                    },
                ],
                script: function () {
                    $.fn.alterClass = function (removals, additions) {

                        var self = this;

                        if (removals.indexOf('*') === -1) {
                            // Use native jQuery methods if there is no wildcard matching
                            self.removeClass(removals);
                            return !additions ? self : self.addClass(additions);
                        }

                        var patt = new RegExp('\\s' +
                            removals.
                                replace(/\*/g, '[A-Za-z0-9-_]+').
                                split(' ').
                                join('\\s|\\s') +
                            '\\s', 'g');

                        self.each(function (i, it) {
                            var cn = ' ' + it.className + ' ';
                            while (patt.test(cn)) {
                                cn = cn.replace(patt, ' ');
                            }
                            it.className = $.trim(cn);
                        });

                        return !additions ? self : self.addClass(additions);
                    }; 
                    $(document).ready(function () {
                        $(this).find('select[name="my-product-toolbar-display"]').on('change', function () {
                            if ($(this).val() === 'block') {
                                $(this).closest('div[data-x-cls="my-products-all"]').find('div[data-x-cls="my-product-item"]').each(function () {
                                    $(this).alterClass("col-*").addClass("col-sm-12 col-md-4 col-lg-3");
                                    $(this).find('div[data-x-cls="my-product-img"]').removeClass("col-4").addClass("col-12");
                                    $(this).find('div[data-x-cls="my-product-info"]').removeClass("col-8 pl-1").addClass("col-12");

                                    // show full small title + remark  --> '[data-x-cls="title-line-text"],[data-x-cls="remark-line-text"]'
                                    // * current apply with title
                                    $(this).find('[data-x-cls="title-line-text"]').each(function(){
                                        $(this).text($(this).attr('data-title-sm'));
                                    })
                                });
                            } else if ($(this).val() === 'list') {
                                $(this).closest('div[data-x-cls="my-products-all"]').find('div[data-x-cls="my-product-item"]').each(function () {
                                    $(this).alterClass("col-*").addClass("col-12");
                                    $(this).find('div[data-x-cls="my-product-img"]').removeClass("col-12").addClass("col-4");
                                    $(this).find('div[data-x-cls="my-product-info"]').removeClass("col-12").addClass("col-8 pl-1");

                                    // show full title + remark --> [data-x-cls="title-line-text"],[data-x-cls="remark-line-text"]
                                    // * current apply with title
                                    $(this).find('[data-x-cls="title-line-text"]').each(function(){
                                        $(this).text($(this).attr('title'));
                                    })
                                });
                            }
                        });
                    })
                },
            }
        },
    })

    editor.DomComponents.addType(idPageButton, {
        model: {
            defaults: {
                name: 'Button Page',
                tagName: 'button',
                dropable: false,
                dragable: false,
                editable: false,
                attributes: {
                    class: "btn btn-light",
                },
                components: ``,
            }
        }
    })

    editor.DomComponents.addType(idToolbarFoot, {
        model: {
            defaults: {
                name: `${label} Toolbar Foot`,
                dropable: false,
                dragable: false,
                tagName: 'div',
                attributes: {
                    class: 'row d-flex justify-content-center align-items-center mt-3',
                    "data-x-cls": "my-product-footer",
                },
                styles: `
                    div[data-x-cls="my-product-footer"] > button {
                        margin-left: 0.25rem;
                        margin-right: 0.25rem;
                    }
                `,
                components: [
                    { type: idPageButton, components: `<i class="fa fa-angle-left" aria-hidden="true"></i>`, name: 'Button Previous Page' },
                    { type: idPageButton, components: `1` },
                    { type: idPageButton, components: `2` },
                    { type: idPageButton, components: `3` },
                    { type: idPageButton, components: `4` },
                    { type: idPageButton, components: `5` },
                    { type: idPageButton, components: `6` },
                    { type: idPageButton, components: `7` },
                    { type: idPageButton, components: `8` },
                    { type: idPageButton, components: `<i class="fa fa-angle-right" aria-hidden="true"></i>`, name: 'Button Next Page' },
                ]
            }
        }
    })
}