import { dataConst } from './const';

export default (editor, opts = {}) => {
    const { block, label, id } = opts;

    editor.BlockManager.add(id, {
        label: label,
        content: { type: id },
        media: `<i class="fa-brands fa-product-hunt fa-3x"></i>`, // ,dataConst.blockIcon,
        category: 'Data Vision',
        select: true,
        ...block,
    });
}
