import loadProducts from './components/products';
import loadToolbar from './components/toolbar';
export default (editor, opts = {}) => {
    const { id, label, productData } = opts;
    const { url, method } = productData;

    // Toolbar
    const idToolbarHead = `${id}-toolbar-head`;
    const idToolbarMatchQueryCount = `${id}-toolbar-query-count`;
    const idPageButton = `${id}-toolbar-foot-page-button`;
    const idToolbarFoot = `${id}-toolbar-foot`;
    loadToolbar(editor, {
        ...opts,
        idToolbarHead: idToolbarHead,
        idPageButton: idPageButton,
        idToolbarFoot: idToolbarFoot,
        idToolbarMatchQueryCount: idToolbarMatchQueryCount,
    })
    // -- Toolbar

    // Table Data
    const idTableData = `${id}-table-data`;
    const idTableDataItem = `${id}-table-data-item`;
    const idTableDataItemLoad = `${id}-table-data-load`;
    loadProducts(editor, {
        ...opts,
        idTableData: idTableData,
        idTableDataItem: idTableDataItem,
        idTableDataItemLoad: idTableDataItemLoad,
    });

    // -- Table Data

    // INIT
    editor.DomComponents.addType(id, {
        model: {
            defaults: {
                name: label,
                tagName: 'div',
                attributes: {
                    class: 'container py-3',
                    "data-x-cls": "my-products-all",
                    "data-url": url,
                    "data-method": method,
                },
                dropable: false,
                components: [
                    { type: idToolbarHead },
                    { type: idTableData },
                    { type: idToolbarFoot },
                ],
                script: function () {
                    class toolbarLoad {
                        constructor(clsThis, resp) {
                            this.myProductAllEle = $(clsThis).closest('div[data-x-cls="my-products-all"]');
                            this.resp = resp;
                        }

                        loadPageCount() {
                            this.myProductAllEle.find('[data-x-cls="my-product-toolbar-query-count"]').text(
                                this.resp.page_count || ''
                            );
                        }
                    }

                    class tableDataLoad {
                        constructor(ele, data) {
                            this.ele = ele;
                            this.data = data;
                        }

                        load() {
                            this.loadSrcImage();
                            this.loadTitle();
                            this.loadPrice();
                            this.loadRemarks();
                        }

                        loadSrcImage() {
                            let imageData = this.data.avatar || 'data:image/svg+xml;base64,';
                            this.ele.find('img[data-x-cls="my-product-img--img"]').attr('src', imageData);
                        }

                        loadTitle() {
                            let titleFullText = this.data.title || '';

                            let titleEle = this.ele.find('[data-x-cls="title-line-text"]');
                            if (titleEle.length > 0) {
                                titleEle.attr('title', titleFullText);

                                let smallTitle = '';
                                if (titleFullText.length > 100) {
                                    smallTitle = `${titleFullText.slice(0, 100)}...`;
                                } else {
                                    smallTitle = `${titleFullText}${" ".repeat(100 - titleFullText)}`;
                                }
                                titleEle.attr('data-title-sm', smallTitle);
                                titleEle.text(smallTitle);
                            }
                        }

                        loadPrice() {
                            let price = this.data.sale_price || '';
                            this.ele.find('[data-x-cls="price-line-text"]').text(price);
                        }

                        loadRemarks() {
                            let remarkTxt = this.data.description || '';
                            let remarkEle = this.ele.find('[data-x-cls="remark-line-text"]');
                            remarkEle.attr('title', remarkTxt);

                            let smallRemark = '';
                            if (remarkTxt.length > 100) {
                                smallRemark = `${remarkTxt.slice(0, 100)}...`;
                            } else {
                                smallRemark = `${remarkTxt}${" ".repeat(100 - remarkTxt)}`;
                            }
                            remarkEle.attr('data-title-sm', smallRemark);
                            remarkEle.text(smallRemark);
                        }
                    }

                    class productCall {
                        constructor(clsThis, myProductDataEle) {
                            this.clsThis = clsThis;
                            this.myProductDataEle = myProductDataEle;

                            // get pattern only one & remove exclude first
                            this.patternItemListEle = $(clsThis).find('div[data-x-cls="my-product-item"][data-is-pattern="true"]').addClass('d-none');
                            this.patternItemListEle.not(":eq(0)").remove();
                            this.patternItemEle = null;
                            if (this.patternItemListEle.length === 1) {
                                this.patternItemEle = $(this.patternItemListEle[0]).clone();
                                $(clsThis).find('div[data-x-cls="my-product-item"]:not([data-is-pattern="true"])').remove();
                            }
                        }

                        params() {
                            let paramData = {};

                            let searchTxt = $(this.clsThis).find('[data-x-cls="my-product-header--search"]').val();
                            if (searchTxt) paramData['search'] = searchTxt;
                            let sortTxt = $(this.clsThis).find('[data-x-cls="my-product-header--sort"]').val();
                            if (sortTxt === 'price-a-z') {
                                paramData['ordering'] = 'sale_price';
                            } else if (sortTxt === 'price-z-a') {
                                paramData['ordering'] = '-sale_price';
                            }
                            return paramData;
                        }

                        callData() {
                            if (this.patternItemEle) {
                                let paramData = this.params();
                                $.fn.callAjaxPublic({
                                    url: $(clsThis).attr('data-url') + '?' + $.param(Object.keys(paramData).length > 0 ? paramData : {}),
                                    type: $(clsThis).attr('data-method'),
                                }).then(
                                    (resp) => {
                                        new toolbarLoad(clsThis, resp).loadPageCount();

                                        let data = resp?.['data'] || null;
                                        if (data && Array.isArray(data)) {
                                            data.map(
                                                (item) => {
                                                    let childEle = this.patternItemEle.clone().removeClass('d-none').attr('data-id', item.id).removeAttr('data-is-pattern');
                                                    new tableDataLoad(childEle, item).load();
                                                    $(myProductDataEle).append(childEle);
                                                }
                                            )
                                        }
                                    },
                                    (errs) => {
                                        console.log('errs:', errs);
                                    }
                                )
                            }
                        }
                    }

                    let clsThis = this;
                    let myProductDataEle = $(this).find('div[data-x-cls="my-product-data"]');
                    if (myProductDataEle.length > 0) {
                        if ($(clsThis).attr('data-url') === '/test/products') {
                            console.log(
                                '%c Response My-Product API return format:',
                                'background: #222; color: red',
                                {
                                    "data": [
                                        {
                                            "id": "{id}",
                                            "title": "{name}",
                                            "available_amount": 40,
                                            "avatar": "{link}",
                                            "description": "{remark}",
                                            "sale_price": 1000,
                                        },
                                    ],
                                    "page_size": 100,
                                    "page_count": 1,
                                    "page_next": 0,
                                    "page_previous": 0
                                }
                            )

                            let productImgSrc = 'data:image/svg+xml;base64,';
                            $.mockjax({
                                url: /\/test\/products(.)*/gm,
                                type: 'GET',
                                contentType: "application/json",
                                responseText: {
                                    "data": [
                                        {
                                            "id": "3a82c590-cb82-4f7c-aba6-cfcb383af8e2",
                                            "title": "Máy in HP",
                                            "code": "PRD.0005",
                                            "available_amount": -40,
                                            "avatar": "https://www.volusion.com/blog/content/images/2021/07/Product-Photos.jpg",
                                            "description": "Máy in màu",
                                            "sale_price": 15000000
                                        },
                                        {
                                            "id": "3a82c590-cb82-4f7c-aba6-cfcb383af8e2",
                                            "title": "Máy in HP",
                                            "code": "PRD.0005",
                                            "available_amount": -40,
                                            "avatar": productImgSrc,
                                            "description": "Máy in màu",
                                            "sale_price": 15000000
                                        },
                                        {
                                            "id": "3a82c590-cb82-4f7c-aba6-cfcb383af8e2",
                                            "title": "Máy in HP",
                                            "code": "PRD.0005",
                                            "available_amount": -40,
                                            "avatar": productImgSrc,
                                            "description": "Máy in màu",
                                            "sale_price": 15000000
                                        },
                                        {
                                            "id": "3a82c590-cb82-4f7c-aba6-cfcb383af8e2",
                                            "title": "Máy in HP",
                                            "code": "PRD.0005",
                                            "available_amount": -40,
                                            "avatar": productImgSrc,
                                            "description": "Máy in màu",
                                            "sale_price": 15000000
                                        },
                                    ],
                                    "page_size": 100,
                                    "page_count": 1,
                                    "page_next": 0,
                                    "page_previous": 0
                                }
                            });
                        }
                        if (!$.fn.callAjaxPublic) {
                            $.fn.callAjaxPublic = function (config) {
                                return new Promise(
                                    (resolve, reject) => {
                                        let { url, method } = config;
                                        let ctx = {
                                            url: url,
                                            type: method, // dataType: 'json',
                                            contentType: "application/json",
                                            processData: true,
                                            data: 'GET',
                                            headers: {
                                            },
                                            success: function (rest, textStatus, jqXHR) {
                                                resolve(rest);
                                            },
                                            error: function (jqXHR, textStatus, errorThrown) {
                                                let resp_data = jqXHR.responseJSON;
                                                if (resp_data && typeof resp_data === 'object') {
                                                    reject(resp_data);
                                                } else if (jqXHR.status === 204) reject({ 'status': 204 });
                                            },
                                        }
                                        return $.ajax(ctx);
                                    }
                                )
                            }
                        }

                        new productCall(clsThis, myProductDataEle).callData();

                        $(clsThis).find('[data-x-cls="my-product-header--search"],[data-x-cls="my-product-header--sort"]').on('change', function () {
                            new productCall(clsThis, myProductDataEle).callData();
                        });
                    }
                },
            },
            init() {
                if (this.components().models[1].components().length === 0) {
                    this.components().models[1].components().add({ type: idTableDataItem });
                }
            },
        },
    });
    // -- INIT
};
